import React, { useEffect, useState } from "react";
import "./Page.css"
import { toast } from "react-toastify";
import { useDebounce } from "use-debounce";
import { toastProp, loggingId, SEARCH_PER_SCREEN, MAX_SEARCH_ENTRY } from "../Util";
import { useQuery, useLazyQuery } from "@apollo/client";
import { Link } from 'react-router-dom'
import {USER_QUERY, USERS_QUERY, HISTORY_QUERY} from "../api/query.js";

function CheckOut(props) {
    const [userText, setUserText] = useState("");
    const [searchQuery] = useDebounce(userText, 50);
    const [searchResults, setSearchResults] = useState([]);
    const [initialized, setInitialized] = useState(false);
    const [userId, setUserId] = useState("");
    const [history, setHistory] = useState([]);
    const {loading: userLoading, data: userListData, error: userError } = useQuery(USERS_QUERY);
    const [loadUser, { data: userData }] = useLazyQuery(USER_QUERY,
                     { "variables": { "_id": userId } });
    const [loadHistory, { data: historyData }] = useLazyQuery(HISTORY_QUERY,
                     { "variables": { "user_id": userId } });
    const [userList, setUserList] = useState({});
    const [title, setTitle] = useState("");
    const [pageNum, setPageNum] = useState(0);
    const [selectedId, setSelectedId] = useState(0);
    const [rented, setRented] = useState([]);

    useEffect(function () {
        async function initialize() {
            if (props.doc.isOpen())
                updateDoc(false);
            else
                props.doc.setCallback(updateDoc);
            console.log("=======================================");
            console.log("UserSearch initialize");
        }

        initialize();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(
        () => {
            console.log("User data updated ");
            updateDoc(false);
        },
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [props.logged]
    );

    useEffect(
        () => {
            console.log("User data updated ");
        },
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [userData]
    );

    useEffect(
        () => {
            console.log("User list loaded");
            console.log(userError);
            console.log(userLoading);
            if (userListData)
            {
                var list = [];
                console.log("User list available");
                const stateStr = {"0": props.text.normal, "1": props.text.overDue, "2": props.text.stopped};
                for (let i = 0; i < userListData.users.length; i++)
                {
                    const user = userListData.users[i];
                    const entry = {"id": user._id, "name": user.name, "level": user.level, "state": stateStr[user.state]};
                    list.push(entry)
                }
                console.log(list);
                props.doc.setUser(list);
                console.log(props.doc.getUser());
                setUserList(props.doc.getUser());
            }
        },
        [userListData, userLoading, userError]
    );

    useEffect(
        () => {
            let rawHist = [];
            if (!historyData || !props.doc.bookReady || !props.doc.rentReady)
                return;
            console.log("History updated ");
//            console.log(historyData);
            for (let i = 0 ; i < historyData["rentLogs"].length ; i++)
            {
                const entry = historyData["rentLogs"][i];
                if (entry["book_state"] !== "0" && entry["book_state"] !== "1")
                    continue;
                const id = entry["book_id"];
                const date = entry["timestamp"].split(" ")[0].replace("-", "/", 2).replace("-", "/")
                rawHist.push({"id": id, "title": props.doc.book[id]["title"], "date": date, "state": entry["book_state"]});
            }
            rawHist.sort((s1, s2) => { return s1["date"] > s2["date"]; });
//            console.log(rawHist);

            let hist = [];
            for (let i = 0 ; i < rawHist.length - 1 ; i++)
            {
                if (rawHist[i]["state"] !== "1")
                    continue;
                const entry  = rawHist[i];
                const id = entry["id"];
                for (let j = i+1 ; j < rawHist.length ; j++)
                {
                    if (rawHist[j]["state"] !== "0" || id !== rawHist[j]["id"])
                        continue;
                    hist.push({"id": entry["id"], "title": entry["title"], "rentDate": entry["date"], "retDate": rawHist[j]["date"]});
                    break;
                }
            }
//            console.log(hist);
            console.log("Set history");
            setHistory(hist);
        },
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [historyData, props.doc.bookReady, props.doc.rentReady]
    );

    async function updateDoc(notify = true)
    {
        console.log("All data loaded " + initialized);

        if (notify)
        {
            const prop = toastProp;
            prop.type = toast.TYPE.SUCCESS;
            prop.render = props.text.succeededToOpen;
            prop.autoClose = 3000;
            prop.toastId = "";
            toast.info(props.text.succeededToOpen, prop);
        }
        console.log("Set title " + props.logged)
        console.log("Done");
        setInitialized(true);
    }

    const showRented = (rent, index) => {
        const id = rent["id"];
        const rentDate = rent["rentDate"];
        const retDate = rent["retDate"];
        const bookName = rent["title"];
        const key = index.toString();
        return (<React.Fragment key={key + "Fragment"}>
                    <tr key={key} className="bookData">
                        <td className="bookData"><Link to={"/search/"+id}>{id}</Link></td>
                        <td className="bookData">{rentDate}</td>
                        <td className="bookData">{retDate}</td>
                    </tr>
                    <tr key={key + "Title"} className="bookName">
                        <td colSpan="3" className="bookName">{bookName}</td>
                    </tr>
                </React.Fragment>
                );
    }

    const selectId = async (id) => {
        if (selectedId === -1 || selectedId !== id)
        {
            console.log("Select " + id);
            setSelectedId(id);
            setRented(props.doc.getRent(searchResults[id].id));
        }
        else
        {
            console.log("Deselect " + id);
            setSelectedId(-1);
        }
    }

    const showUser = (user, index) => {
        const key = index.toString();
        return (
                <div>
                    <table><tbody>
                        <tr key={key} className="searchTr" onClick={async ()=> await selectId(index)}>
                            <td className="bookData">{user.id}</td>
                            <td className="bookData">{user.name}</td>
                            <td className="bookData">{user.state}</td>
                            <td className="bookData">{user.rent}</td>
                        </tr>
                    </tbody></table>
                        {index === selectedId && showEntries(rented) }
                </div>
                );
    }

    const showUsers = (result) => {
        return (<div>
                    {
                        result.map((user, index) => {
                            return showUser(user, index);
                        })
                    }
                    {
                        result.length === 0 &&
                        <table><tbody>
                            <tr key="None"><td colSpan="3">{props.text.noEntry}</td></tr>
                        </tbody></table>
                    }
                </div>);
    }

    const showEntries = (result) => {
        return (<>
                    <table><tbody>
                    <tr key="ID">
                        <th id="id">{props.text.id}</th>
                        <th id="rentDate">{props.text.rentDate}</th>
                        <th id="returnDate">{props.text.returnDate}</th>
                    </tr>
                    {
                        result.map((rent, index) => {
                            return showRented(rent, index);
                        })
                    }
                    {
                        result.length === 0 && <tr key="None"><td colSpan="3">{props.text.noEntry}</td></tr>
                    }
                    </tbody></table>
                </>);
    }
    useEffect(
        () => {
            async function findUsers(text) {
                let results = [];

//                for (let i = 0 ; i < userList.length ; i++) {
                  for (const key in userList) {
                    const row = userList[key];
                    if (results.length >= MAX_SEARCH_ENTRY) break;
                    if ((row.name && row.name.toString().includes(text)) ||
                        (row.id.toLowerCase() === text.toLowerCase()))
                    {
                        results.push(row);
                    }
                }
                results.sort(function(a,b) { return a['text'] > b['text']; });
                return results;
            }
            async function query() {
                if (searchQuery) {
                    let sr = await findUsers(searchQuery);
                    if (sr.length > 0)
                        setSearchResults(sr);
                    else
                        setSearchResults([]);
                    if (sr.length === 1)
                        setSelectedId(sr[0].index);
                } else {
                    setSearchResults([]);
                }
                setPageNum(0);
            }
            query();
        },
        [searchQuery, props]
    );

    return (
        <div id="checkOut">
            <div id="title">
                <h2>
                    {props.text.userSearch}
                </h2>
            </div>
            <div id="checkOutInput" hidden={!props.doc.adminMode}>
                <input type="text" id="searchInput"
                    placeholder={props.text.idHolder}
                    value={userText}
                    onChange={(event) => {
                        setUserText(event.target.value);
                    }} />
                <div>
                    { showUsers(searchResults) }
                </div>
            </div>
        </div>
    );
}

export default CheckOut;
